<template>
  <div class="pd3 border-box">
    <section id="page_main" class="mg-b15">
        <article>
          <h4 class="mp0 mg-b10 color-333">오늘의 사또</h4>
          <div class="pd15 bg-fff-0_6 radius10 txt-c" style="box-shadow:0 1px 1px #bbb;">
              <p class="mg0 font14" v-if="state.nowQuiz.quiz_url">
                참여시간: 08:00 ~ 19:59<br>
                정답공개: 20:45 ~<br>
                <strong class="inline-block mg-t10">*위 시간을 준수해주세요.</strong>
              </p>
              <div class="txt-c mg-b10">
                  <strong class="mg-r3 inline-block radius50p txt-c border-box bg-ddd" style="width:9vw; height:9vw;"
                  v-for="idx in 6" :key="idx">?</strong>
                  <strong class="mg-r3">+</strong>
                  <strong class="mg-r3 inline-block radius50p txt-c border-box bg-ddd" style="width:9vw; height:9vw;">?</strong>
              </div>
              <div class="txt-c" v-if="!state.isShowNumberBoard">
                <button class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee" style="background:#fd0;" @click="showNumberBoard();"><strong>참여</strong></button>
              </div>
              <div class="mg-b10 border-ddd radius10 pd10" v-if="state.isShowNumberBoard">
                <h5 class="mg0 mg-b5 txt-l">힌트</h5>
                <div v-html="state.baseHint" class="mg-b10"></div>
                <div v-if="state.isShowHint" v-html="state.moreHint"></div>
                <h4 class="mg0"><strong class="font10 color-f00">*모든 힌트는 보너스 숫자를 포함하지 않습니다.</strong></h4>
                <div v-if="!state.isShowHint" class="mg-t10">
                  <button class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee bg-fff" @click="showMoreHint();"><strong>더보기</strong></button>
                </div>
              </div>
              <div class="pd-v10 mg-b10 border-v-dashed-ddd" v-if="state.isShowNumberBoard">
                <div class="mg-b10">
                  <div :class="{'mg-b3': idx != 5}" v-for="idx in 5" :key="idx">
                    <span class="inline-block radius50p border-box font11 color-fff" style="width:7.8vw; height:7.8vw;" @click="pickTheBall($event)"
                    v-for="idx2 in 9" :key="idx2" :style="{background: state.ballColors[idx - 1]}"
                    :class="{'mg-r3': idx2 != 9}">{{((idx - 1) * 9) + idx2}}</span>
                  </div>
                </div>
                <h4 class="txt-c mg0">
                  {{state.pickedBalls.length}} / 6 
                </h4>
              </div>
              <div class="txt-c" v-if="state.isShowNumberBoard">
                <button class="w80 pd10 border0 outline0 mg-r5 radius50 font12 border-eee" style="background:#fd0;" @click="sumitTheNumber();"><strong>제출</strong></button>
                <button class="w60 pd10 border0 outline0 mg-r5 radius50 font12 border-eee bg-fff" @click="showNumberBoard();"><strong>취소</strong></button>
              </div>
          </div>

        </article>
    </section>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import axios from 'axios';
import { useStore } from "vuex";
import node from '../config/backEnd.js';

export default {
  setup() {
    const store = useStore();
    const state = reactive({
      newAnswer: '',
      oldAnswer: '',
      viewOldQuiz: '',
      viewOldQuizHint: '',
      oldQuizList: [
        // {
        //   quiz_url: '',
        //   hint_url: '',
        //   answer_url: '',
        //   release_timing_kor: ''
        // }
      ],
      nowQuiz: {
        quiz_url: '',
        hint_url: ''
      },
      oldQuizListCnt: 5,

      control_now_quiz: {
        hint: false
      },


      ballColors: ['#F15A5A', '#F0C419', '#4EBA6F', '#2D95BF', '#955BA5'],
      pickedBalls: [],

      randomNumArr7: [],
      baseHint: '',
      moreHint: '',

      isShowNumberBoard: false,
      isShowHint: false,
    })

    //ref
    const newAnswer = ref(null);
    //ref


    //method

    const getRandomNum = () => {
      
      let getShuffleArr = (arr, length, shuffledArr) => {
        let random = (min, max) => {
          return Math.floor((Math.random() * (max - (min - 1)) + min));
        }
        let tmpArr = arr;
        let tmpShuffledArr = shuffledArr == undefined ? [] : shuffledArr;

        tmpShuffledArr.push(tmpArr.splice(random(0, tmpArr.length - 1), 1)[0]);

        if(tmpShuffledArr.length !== length) return getShuffleArr(tmpArr, length, tmpShuffledArr);

        return tmpShuffledArr;
      }

      let arr45 = [];
      for(let i = 0; i < 45; i++){
        arr45.push(i + 1);
      }

      let shuffledArr = getShuffleArr(arr45, 7);
      shuffledArr[0] = '+ ' + shuffledArr[0];


      state.randomNumArr7 = shuffledArr.sort(function(a, b)  {
        if(a > b) return 1;
        if(a === b) return 0;
        if(a < b) return -1;
      });

      state.randomNumArr7.push(state.randomNumArr7.shift());  //첫번째와 마지막번째 위치 변경

      console.log(state.randomNumArr7);

      getBaseHint();
      getMoreHint();
    }

    const getBaseHint = () => {
      let tmpArr = [];
      for(let i = 0; i < 6; i++){
        tmpArr.push(state.randomNumArr7[i + 1]);
      }
      let ballColors = [];

      tmpArr.forEach((el) => {
        //숫자 컬러
        if(el < 10){ballColors.push(state.ballColors[0])}
        else if(el < 19){ballColors.push(state.ballColors[1])}
        else if(el < 28){ballColors.push(state.ballColors[2])}
        else if(el < 37){ballColors.push(state.ballColors[3])}
        else{ballColors.push(state.ballColors[4])}

      })

      state.hint += '<p>';
      for(let i = 0; i < ballColors.length; i++){
        state.baseHint += '<strong class="inline-block radius50p border-box font11 color-fff mg-r3" style="width:7.8vw; height:7.8vw; background:' + ballColors[i] + ';">?</strong>';
      }
      state.hint += '</p>';
    }

    const getMoreHint = () => {
      let tmpArr = [];
      let odds = 0;
      let total = 0;
      
      for(let i = 0; i < 6; i++){
        tmpArr.push(state.randomNumArr7[i + 1]);
      }

      let arrToStr = tmpArr.join('').split('').sort().join('');
      let colomns = '';

      tmpArr.forEach((el) => {
        if(el % 2 == 1){  //홀짝
          odds++;
        }
        total += el;  //합계
        let alphabets = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i'];
        for(let i = 0 ; i < 9; i++){
          if(el == (1 + i) || el == (10 + i) || el == (19 + i) || el == (28 + i) || el == (37 + i)){
            colomns += alphabets[i];
          }
        }
      })

      colomns = colomns.split('').sort().join('');

      let duplStr = colomns[0];
      let duplCnt = 0;
      let changeStr = colomns[0];

      for(let i = 0; i < colomns.length; i++){
        if(colomns[i] == duplStr){
          duplCnt++;
        }else{
          changeStr += duplCnt + colomns[i];
          duplCnt = 1;
        }
        duplStr = colomns[i];
        if(i == (colomns.length - 1)){
          changeStr += duplCnt;
        }
      }

      //출력

      state.moreHint += `<p class="mg0 mg-b10">
      odd${odds}even${tmpArr.length - odds}
      </p>`;
      state.moreHint += `<p class="mg0 mg-b10">
      combination/sort:${arrToStr}
      </p>`;
      state.moreHint += `<p class="mg0 mg-b10">
      total${total}
      </p>`;
      state.moreHint += `<p class="mg0 mg-b10">
      ${changeStr}
      </p>`;

    }

    const pickTheBall = (target) => {
      let thisTarget = target.target;
      let tmpArr = state.pickedBalls;

      if(!thisTarget.classList.contains('op0_2')){
        if(tmpArr.length > 5){
          alert('6개를 넘을 수 없습니다.');
          return;
        }
        thisTarget.classList.add('op0_2');
        tmpArr.push(Number(thisTarget.innerHTML));
      }else{
        thisTarget.classList.remove('op0_2');

        let elemIdx = tmpArr.indexOf(Number(thisTarget.innerHTML));
        if (elemIdx > -1){
          tmpArr.splice(elemIdx, 1);
        }
      }

      state.pickedBalls = tmpArr.sort(function(a, b)  {
        if(a > b) return 1;
        if(a === b) return 0;
        if(a < b) return -1;
      });

      console.log(state.pickedBalls);
    }

    const showNumberBoard = () => {
        state.isShowNumberBoard = !state.isShowNumberBoard;
        state.pickedBalls = [];
    }

    const showMoreHint = () => {
      if(state.isShowHint) return;
      if(confirm('힌트를 보려면 하트가 소진됩니다.\n진행하시겠습니까?')){
        state.isShowHint = true;
      }
    }


    const sumitTheNumber = () => {
      if(state.pickedBalls.length != 6){
        alert('6개의 숫자를 선택해주세요.');
        return;
      }

      let matchCnt = 0;
      let matchBonus = false;

      if(confirm(state.pickedBalls + '\n위 숫자로 제출하시겠습니까?')){
        for(let i = 0; i < state.pickedBalls.length; i++){
          for(let j = 0; j < state.randomNumArr7.length; j++){
            if(j == (state.randomNumArr7.length - 1)){
              if(state.pickedBalls[i] == state.randomNumArr7[j].substr(2, state.randomNumArr7[j].length)){
                matchBonus = true;
              }
            }else{
              if(state.pickedBalls[i] == state.randomNumArr7[j]){
                matchCnt++;
                break;
              }
            }
          }
        }

        switch(matchCnt){
          case 3:
            alert(state.randomNumArr7 + '\n5등 당첨!\n나의 숫자\n' + state.pickedBalls);
          break;
          case 4:
            alert(state.randomNumArr7 + '\n4등 당첨!\n나의 숫자\n' + state.pickedBalls);
          break;
          case 5:
            if(matchBonus){
              alert(state.randomNumArr7 + '\n2등 당첨!\n나의 숫자\n' + state.pickedBalls);
            }else{
              alert(state.randomNumArr7 + '\n3등 당첨!\n나의 숫자\n' + state.pickedBalls);
            }
          break;
          case 6:
            alert(state.randomNumArr7 + '\n1등 당첨!\n나의 숫자\n' + state.pickedBalls);
          break;
          default:
            alert(state.randomNumArr7 + '\n꽝!\n나의 숫자\n' + state.pickedBalls);
            break;
        }
      }
    }
























    const submitTheAnswer = (isNew, seq_q) => {
      let answer = '';
      let isNewQuiz = isNew ? true : false;
      answer = isNew ? state.newAnswer : state.oldAnswer;

      if(confirm('\'' + answer + '\' (으)로 제출하시겠습니까?')){
        axios.post(`${node.nodeUrl}/submitTheAnswer`, {
          seq_q: seq_q,
          seq_m: store.state.userInfo.seq_m,
          nickname: store.state.userInfo.nickname,
          isNew: isNewQuiz,
          answer: answer
        }).then((res) => {
          // console.log(res.data);
          if(res.data.timeOver){
            alert('제출시간이 초과되었습니다.\n새로고침 부탁드립니다.');
            return;
          }

          let tmpData = {};
          tmpData = res.data;
          tmpData.isNew = isNewQuiz;

          store.state.resultPopData = tmpData;
          // console.log(store.state.resultPopData);
          store.commit('popupControl', {isOpen: true, name: 'result'});

          // state.todos = res.data;
        });
        // alert('제출되었습니다.');
        // isNew ? state.newAnswer = '' : state.oldAnswer = '';
      }
    }

    const getOldQuiz = (oldQuizSeq) => {
      state.viewOldQuiz = oldQuizSeq;
      state.oldAnswer = '';
      state.viewOldQuizHint = '';
    }

    const getOldQuizHint = (oldQuizSeq) => {
      if(confirm('광고가 포함됩니다.\n진행하시겠습니까?')){
        setTimeout(function(){
          state.viewOldQuizHint = oldQuizSeq;
        })
      }
    }

    const getNowQuiz = () => {  //현재퀴즈 가져오기
      axios.post(`${node.nodeUrl}/getNowQuiz`).then((res) => {
        if(!res.data.length) return;
        state.nowQuiz = res.data[0];
      });
    }

    const showNowHint = () => { //현재퀴즈 힌트보기
      if(state.control_now_quiz.hint) return;
      if(confirm('광고가 노출됩니다.\n진행하시겠습니까?')){
        state.control_now_quiz.hint = true;
      }
    }

    const getOldQuizList = () => {
      axios.post(`${node.nodeUrl}/getOldQuizList`, {cnt: state.oldQuizListCnt}).then((res) => {
        state.oldQuizList = res.data;
        state.oldQuizListCnt += 5;

        // console.log(state.oldQuizList);
        // if(!res.data.length) return;

        // state.nowQuiz = res.data[0];
      });
    }

    //method

    //create
    // getNowQuiz();
    // getOldQuizList();
    getRandomNum();
    //create



    return {
      //var
      state,
      //var

      //ref
      newAnswer,
      //ref
      
      //method
      submitTheAnswer,
      getOldQuiz,
      getOldQuizHint,
      getNowQuiz,
      getOldQuizList,
      showNowHint,

      pickTheBall,
      showNumberBoard,
      showMoreHint,
      getRandomNum,
      getBaseHint,
      getMoreHint,
      sumitTheNumber,
      //method

      store,


    }
  },
}
</script>